Window.newGame = function () {
  var initializeCanvas; var initializeKeyControls; var initializeWorld; var intervalFunction; var play;
  // 1. REQUIRE DEPENDENCIES
  var objects; var Man; var Clicker; var Blank;
  objects = require('./objects.js');
  Man = require('./man.js');
  Clicker = require('./clicker.js');
  Blank = require('./blank.js');

  // 2. INITIALIZE CANVAS
  initializeCanvas = function () {
    window.onload = function () {
      var canvas; var ctx;
      canvas = document.getElementById("canvas");
      canvas.height = window.innerHeight;
      canvas.width = window.innerWidth;
      ctx = canvas.getContext('2d');
      this.canvas = canvas;
      this.ctx = ctx;
      ctx.clearRect(0, 0, canvas.width, canvas.height);
    }.bind(this);
  };

  // 3. INITIALIZE KEY CONTROLS
  initializeKeyControls = function (player) {
  };

  // 4. INITIALIZE WORLD
  initializeWorld = function () {
    var clicker; var ff;
    clicker = new Clicker(objects.length);
    clicker.init();
    objects.push(clicker);
    for (ff=0; ff < 300; ff++) {
      objects.push(new Man(objects.length, 100+Math.random()*650, 20+Math.pow((Math.random()*20), 2)));
    }
    objects.push(new Man(objects.length, 1000000, 1000000));
  };

  // 5. DEFINE INTERVAL FUNCTION
  intervalFunction = function () {
    var xx;
    ctx.fillStyle = '#667766';
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    for (xx=0; xx < objects.length; xx++) {
      if (objects[xx]) {
        objects[xx].draw(ctx);
        objects[xx].act();
      }
    }
  };

  // 5. PLAY
  play = function () {
    var interval; var xx;
    initializeWorld();
    interval = setInterval(intervalFunction, 32);
  };
  initializeCanvas();
  play();
};

Window.newGame();



