var Man;
Man = function (index, x, y) {
  this.index = index;
  this.name = 'man';
  this.moveSpeed = 2;
  this.active = false;
  this.color = '#bb0000';
  this.radius = 6;
  this.speed = {
    x: 0,
    y: 0,
  };
  this.pos = {
    x: x,
    y: y,
  };
};

Man.prototype.draw = function (ctx) {
  ctx.beginPath();
  ctx.arc(this.pos.x, this.pos.y, this.radius, 0, 2*Math.PI);
  ctx.fillStyle = this.color;
  ctx.fill();
};

Man.prototype.act = function () {
  this.pos.x += this.speed.x;
  this.pos.y += this.speed.y;
  this.color = this.active ? '#ee3333' : '#bb0000';
};

Man.prototype.goTo = function (dest) {
  this.moveTowards(dest);
  window.clearInterval(this.interval);
  this.interval = window.setInterval(function () {
    var xOff; var yOff;
    tempDest = {
      x: dest.x,
      y: dest.y,
    };
    xOff = (Math.random()*100)-50;
    yOff = (Math.random()*100)-50;
    tempDest.x += xOff;
    tempDest.y += yOff;
    this.moveTowards(tempDest);
    if (Math.abs(this.pos.x - dest.x) < xOff+8 && Math.abs(this.pos.y - dest.y) < yOff+8) {
      this.speed = {
        x: 0,
        y: 0,
      };
      window.clearInterval(this.interval);
    }
  }.bind(this), 1800-(Math.random()*1800));
};

Man.prototype.moveTowards = function (dest) {
  this.speed.x = -((this.pos.x - dest.x)/(Math.sqrt(
    Math.pow((this.pos.x - dest.x), 2) + Math.pow((this.pos.y - dest.y), 2)
  )/this.moveSpeed));
  this.speed.y = -((this.pos.y - dest.y)/(Math.sqrt(
    Math.pow((this.pos.x - dest.x), 2) + Math.pow((this.pos.y - dest.y), 2)
  )/this.moveSpeed));
};

module.exports = Man;



